<template>
  <div class="settle_item_order">
    <!-- file donwload-->
    <div style="text-align: center;margin-top: 20px;">
        <div class="block" style="margin-top:10px;margin-bottom:10px;">
          <el-date-picker
            v-model="date"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            >
          </el-date-picker>
        </div>
        <el-button type="primary" @click="downloadFile" style="margin-top:10px;">导出</el-button>
        <el-button type="danger" @click="emptySettleOrder = true" style="margin-top:10px;">清空</el-button>


    </div>


  </div>
</template>

<script>
import axios from 'axios'
import url from './middle/url.js'


export default {
  data () {
    return {
      date: [],
      proUrl: url.providerUrl,
      downFileOrderUrl: url.downFileOrderUrl,
      
      data: [],
      total: 0,
      pageSize: 0,
      pageUrl: url.providerUrl+ 'queryItemNumberSettleOrder/',

    }
  },
  created(){
    this.getSettleOrder()
  },
  mounted() {
  },
  methods: {
    downloadFile() {
      let start = this.date[0]
      let end = this.date[1]
      let url = this.downFileOrderUrl+'?start='+start+'&end='+end;
      
      window.open(url)
    },
    getSettleOrder() {
      let _this = this
      axios.get(this.proUrl + 'queryItemNumberSettleOrder/1')
      .then(e => {
        console.log(e)
        _this.data = e.data.message.dataList
        _this.total =e.data.message.num
        _this.pageSize = e.data.message.pageSize
      })
    },
    listenPageOrder(data) {
      console.log(data)
      this.data = data
    },
    downSettleOrder(){
      this.$confirm('此操作将下载错误订单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          document.getElementById("postSubmit").click()
          this.$message({
            type: 'success',
            message: '下载成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消下载!'
          });
        });
    },
  },
  components: {

  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.cell{
  text-align: center;
}


</style>



