<template>
  <div class="settle_order">
    <!-- file donwload-->
    <!-- <input type="file" id="file" name="file" @change="postFile" style="margin: 10px;">
    <el-button type="success" style="margin: 10px;" @click="downFile">导出</el-button>
     -->
    <form method="post" :action="downFileUrl" enctype="application/x-www-form-urlencoded" >
      <input type="text" :value="JSON.stringify(data)" name="data" v-show="false">
      <input type="submit"  style="margin:10px;" value="导出"></input>
    </form>

    <el-table
        :data="data"
        stripe
        style="width: 100%">
        <el-table-column
          prop="balance"
          width="180"
          label="账户余额">
        </el-table-column>
        <el-table-column
          prop="expenditure"
          width="180"
          label="支出">
        </el-table-column>
        <el-table-column
          prop="goodsName"
          width="280"
          label="商品名称">
        </el-table-column>
        <el-table-column
          prop="inDate"
          width="280"
          label="入账时间">
        </el-table-column>
        <el-table-column
          prop="income"
          width="180"
          label="收入">
        </el-table-column>
        <el-table-column
          prop="orderNumber"
          width="280"
          label="商户订单号">
        </el-table-column>
        <el-table-column
          prop="otherAccount"
          width="280"
          label="对方账户">
        </el-table-column>
        <el-table-column
          prop="otherName"
          width="280"
          label="对方名称">
        </el-table-column>
        <el-table-column
          prop="payNumber"
          width="280"
          label="交易流水号">
        </el-table-column>
        <el-table-column
          prop="payType"
          width="180"
          label="账务类型">
        </el-table-column>
        <el-table-column
          prop="payway"
          width="180"
          label="付款方式">
        </el-table-column>
        <el-table-column
          prop="reason"
          width="180"
          label="原因">
        </el-table-column>
        <el-table-column
          prop="remark"
          width="280"
          label="备注">
        </el-table-column>
        <el-table-column
          prop="transactionNumber"
          width="280"
          label="支付宝交易账号">
        </el-table-column>
      </el-table>
  </div>
</template>

<script>
import axios from 'axios'
import url from './middle/url.js'
import $ from 'jquery'
export default {
  data () {
    return {
      proUrl: url.providerUrl,
      postFileUrl: url.wrongSettleUrl,
      downFileUrl: url.downFileUrl,
      isHaveFile: true,

      data: [],
      total: 0,
      pageSize: 0,
      pageUrl: url.providerUrl+ 'querySettleOrder/'
    }
  },
  created(){
    this.getSettleOrder()
  },
  mounted() {
  },
  methods: {

    downFile() {
      let url = this.downFileUrl
      let data = this.data
      // axios({
      //   method: 'post',
      //   url,
      //   data: {
      //     data
      //   },
      //   responseType: 'blob'
      // }).then(e => {
      //   this.download(e)
      // })
      $.ajax({
        type: 'post',
        data: JSON.stringify({
            "alipaySettles": data
        }),
        url,
        contentType: 'application/json;charset=UTF-8',
        success(e) {
          console.log(e)
        },
        error(e) {
          console.log(e)
        }
      })
    },
    postFile(event) {
      let _this = this 
      let formdata = new FormData()
      formdata.append('file', event.target.files[0])
      console.log(formdata.get('file'))
      axios({
        url: this.postFileUrl,
        method: 'post',
        data: formdata,
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      })
      .then((e)=>{
        if(e.status === 200) {
          _this.isHaveFile = true
          _this.data = e.data
        }
      })
    },
    getSettleOrder() {
      let _this = this
      axios.get(this.proUrl + 'querySettleOrder/1')
      .then(e => {
        console.log(e)
        _this.data = e.data.message.dataList
        _this.total =e.data.message.num
        _this.pageSize = e.data.message.pageSize
      })
    },
    listenPageOrder(data) {
      this.data = data
    },
    downSettleOrder(){
      this.$confirm('此操作将下载错误订单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          document.getElementById("postSubmit").click()
          this.$message({
            type: 'success',
            message: '下载成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消下载!'
          });
        });
    },
  },
  components: {

  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.cell{
  text-align: center;
}


</style>



